/* Swiper */
.swiper-nav {
  position: absolute;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  pointer-events: none;
}
@media only screen and (max-width: 991px) {
  .swiper-nav {
    justify-content: center;
    gap: 20px;
  }
}
.swiper-nav .swiper-btn-prev,
.swiper-nav .swiper-btn-next {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 37px;
  background-color: #000000;
  border-radius: 10px 6px 10px 6px;
  transform: skewX(-8deg);
  cursor: pointer;
  pointer-events: auto;
  transition: background-color 0.3s ease;
}
.swiper-nav .swiper-btn-prev:hover,
.swiper-nav .swiper-btn-next:hover {
  background-color: rgba(0, 0, 0, 0.7);
}
.swiper-nav .swiper-btn-prev svg,
.swiper-nav .swiper-btn-next svg {
  transform: skewX(8deg);
}
.swiper-nav .swiper-button-disabled {
  filter: grayscale(100%);
  cursor: default;
}

/* Swiper pagination */
.swiper-pagination {
  display: none;
  align-items: center;
  justify-content: center;
  gap: 10px;
  position: absolute;
  top: unset !important;
  bottom: 16px !important;
  z-index: 12;
}
@media only screen and (max-width: 991px) {
  .swiper-pagination {
    display: flex;
  }
}
.swiper-pagination .swiper-pagination-bullet {
  width: 20px;
  height: 20px;
  opacity: 0.5;
  background-color: #ffffff;
  margin: 0 !important;
}
.swiper-pagination .swiper-pagination-bullet-active {
  opacity: 1;
}

